import "../../../../common/extensions/global";
import DtPicker from "../components/dateTimePickerHelper";
import "../common/extensions/jquery-et";
import Global from "../components/Global";


(() => {
    let createDtPicker: DtPicker;
    let modifyDtPicker: DtPicker;

    /**
     * 初始化DtPicker
     */
    const initDtPicker = () => {
        createDtPicker = new DtPicker("#createDateTimePicker", {
            widgetPositioning: {
                horizontal: 'auto',
                vertical: 'top'
            }
        });
        modifyDtPicker = new DtPicker("#modifyDateTimePicker", {
            widgetPositioning: {
                horizontal: 'auto',
                vertical: 'top'
            }
        });

        createDtPicker.on("dp.change", (e: any) => {
            if (e.date) {
                let newDate = createDtPicker.getDt();
                if (newDate) {
                    modifyDtPicker.setMinDt(createDtPicker.getDt());
                }
            }
        });

        modifyDtPicker.on("dp.change", (e: any) => {
            if (e.date) {
                let newDate = modifyDtPicker.getDt();
                if (newDate) {
                    createDtPicker.setMaxDt(newDate);
                }
            }
        });

        $("#curTime").click(() => {
            modifyDtPicker.setDt(new Date());
        });
    }

    initDtPicker();

    let firstFlag = true;
    const initSelector = () => {
        if (firstFlag) {
            $('#parentId').parent().children("button").click();
            $('#status').parent().children("button").click();
            firstFlag = false;
        }
    }

    const resetEdit = () => {
        $('#taxonomyName').val("");
        $('#parentId').val("").selectpicker('refresh');
        createDtPicker.clear();
        modifyDtPicker.clear();
        $('#status').val("").selectpicker('refresh');
    }
    $(".create").click(() => {
        resetEdit();
        $('#editModal').modal('show');
        initSelector();
    });

    const closeDialog = () => {
        var modal = $('#editModal');
        if (modal.length > 0) {
            modal.modal('hide');
        } else {
            Global.closeDialog('ok');
        }
    }

    const submitEssayType = () => {
        let taxonomyEdit = `/api/EssayType/Edit`;

        var content = $("#taxonomyName").val();
        if (!content) {
            alert("分类名称不能为空！");
            return;
        }

        let pid_level = (<string>$('#parentId').val()).split("|")
            .map((item: any) => {
                return parseInt(item);
            });

        let obj: any = {};
        obj.name = $('#taxonomyName').val();
        obj.parentId = pid_level[0];
        obj.level = pid_level[1] + 1;
        obj.create = $('#create').val();
        obj.modify = $('#modify').val();
        obj.status = $("#status").val();
        let id: any = $("#editModal").data("id");
        if (id) {
            obj.id = id;
        } else {
            obj.id = 0;
        }

        Global.showLoading();
        $.ajax({
            url: taxonomyEdit,
            data: obj,
            type: "POST",
            dataType: "JSON",
            async: true,
            success(msg) {
                if (msg.Code === 0) {
                    Global.alert("操作成功");
                    closeDialog();
                } else {
                    Global.alertError(msg.Error);
                }
            },
            error() {
                alert("网络错误！");
            },
            complete() {
                Global.hideLoading();
            }
        });
    }

    const submitTag = () => {
        let taxonomyEdit = `/api/Tag/Edit`;

        var content = $("#taxonomyName").val();
        if (!content) {
            alert("分类名称不能为空！");
            return;
        }

        let obj: any = {};
        obj.name = $('#taxonomyName').val();
        obj.create = $('#create').val();
        obj.modify = $('#modify').val();
        obj.status = $("#status").val();
        let id: any = $("#editModal").data("id");
        if (id) {
            obj.id = id;
        } else {
            obj.id = 0;
        }

        Global.showLoading();
        $.ajax({
            url: taxonomyEdit,
            data: obj,
            type: "POST",
            dataType: "JSON",
            async: true,
            success(msg) {
                if (msg.Code === 0) {
                    Global.alert("操作成功");
                    closeDialog(); closeDialog();
                } else {
                    Global.alertError(msg.Error);
                }
            },
            error() {
                alert("网络错误！");
            },
            complete() {
                Global.hideLoading();
            }
        });
    }

    /**
     * 确认回复
     */
    $("#submitEdit").click(() => {
        const lowerUrl = location.href.toLocaleLowerCase();
        console.log(lowerUrl);
        if (lowerUrl.endsWith('tag')) {
            submitTag();
        } else if (lowerUrl.endsWith('essaytype')) {
            submitEssayType();
        }
    });

    $("#closeEdit").click((e) => {
        e.preventDefault();

        closeDialog();
    });

})();